@font-face {
  font-family: "PT Mono";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: medium;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Courier", monospace;
  font-size: 16px;
  line-height: 22px;
  color: #666666; }

a {
  text-decoration: none; }

img {
  width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: #1b2d37;
  background-image: url("../img/bg-header-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: cover;
  overflow: hidden; }

.page-header__wrapper {
  max-width: 1411px;
  margin: 0 auto;
  padding: 34px 7px 126px 23px; }

@media (max-width: 1023px) {
  .page-header {
    background-image: url("../img/bg-header-tablet@1x.jpg"); } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-tablet@2x.jpg"); } }

@media (max-width: 1023px) {
  .page-header__wrapper {
    padding: 18px 24px 162px 24px; } }

@media (max-width: 767px) {
  .page-header {
    background-image: url("../img/bg-header-mobile@1x.jpg"); } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/bg-header-mobile@2x.jpg"); } }

@media (max-width: 767px) {
  .page-header__wrapper {
    padding: 18px 5px 72px 16px; } }

.main-nav {
  display: flex;
  align-items: center;
  margin-bottom: 170px; }

.main-nav__list {
  display: flex;
  width: 441px;
  margin: 0;
  margin-left: auto;
  margin-right: 38px;
  padding: 0;
  list-style: none; }

.main-nav__item {
  margin-right: 88px; }
  .main-nav__item:hover {
    opacity: 0.8; }
  .main-nav__item:active, .main-nav__item:focus {
    opacity: 1; }
  .main-nav__item:last-child {
    margin-right: 0; }

.main-nav__icon {
  margin-right: 10px;
  vertical-align: middle; }

.main-nav__info {
  font-size: 16px;
  line-height: 18px;
  color: #ffffff; }

.main-nav__button {
  width: 180px;
  padding: 14px 24px;
  font-size: 14px;
  line-height: 16px;
  border: 1px solid #ffffff; }
  .main-nav__button:hover, .main-nav__button:focus {
    background-color: #00b2ff;
    border: 1px solid #00b2ff; }
  .main-nav__button:active {
    opacity: 0.8; }

@media (max-width: 1023px) {
  .main-nav {
    margin-bottom: 45px; }
  .main-nav__list {
    width: 83px;
    margin-right: 0; }
  .main-nav__item {
    margin-right: 33px; }
  .main-nav__icon {
    margin-right: 0; }
    .main-nav__icon--mail {
      width: 26px;
      height: 19px; }
    .main-nav__icon--phone {
      width: 24px;
      height: 24px; }
  .main-nav__info,
  .main-nav__button {
    display: none; } }

@media (max-width: 767px) {
  .main-nav__item {
    margin-right: 23px; } }

.logo {
  display: flex;
  justify-content: space-between;
  width: 202px; }
  .logo:hover, .logo:focus {
    opacity: 0.8; }
  .logo:active {
    opacity: 1; }

.logo_image {
  width: 52px;
  height: 52px; }

.logo__text {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding-top: 5px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  font-weight: bold;
  color: #ffffff; }

.logo__slogan {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: #788891; }

@media (max-width: 767px) {
  .logo_image {
    width: 38px;
    height: 38px; }
  .logo__text {
    display: none; } }

.main-screen {
  position: relative;
  display: flex; }

.main-screen__info {
  z-index: 2;
  display: flex;
  flex-direction: column;
  margin-right: 70px; }

.main-screen__info h2 {
  width: 718px;
  margin-top: 58px;
  margin-bottom: 30px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }

.main-screen__info p {
  position: relative;
  width: 618px;
  margin: 0;
  margin-bottom: 45px;
  padding-left: 100px;
  font-size: 22px;
  line-height: 34px;
  color: #ffffff; }
  .main-screen__info p::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 0;
    width: 66px;
    height: 2px;
    background-color: #ffffff; }

.main-screen__button {
  width: 407px;
  margin-left: 100px;
  margin-bottom: 133px;
  padding: 17px 24px;
  font-size: 16px;
  line-height: 19px;
  background-color: #00b2ff;
  border: 1px solid #00b2ff;
  cursor: pointer; }
  .main-screen__button:hover, .main-screen__button:focus {
    background-color: transparent;
    border: 1px solid #ffffff; }
  .main-screen__button:active {
    opacity: 0.8; }

.main-screen__image {
  position: absolute;
  top: 0;
  right: -83px; }

.main-screen__image img {
  width: 705px;
  height: 515px; }

.main-screen__scroll {
  width: 105px;
  margin-left: -43px;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  background-color: transparent;
  cursor: pointer;
  border: none;
  opacity: 0.5;
  transform: rotate(-90deg); }
  .main-screen__scroll:hover, .main-screen__scroll:focus {
    opacity: 0.3; }
  .main-screen__scroll:active {
    opacity: 0.5; }

@media (max-width: 1023px) {
  .main-screen__info {
    margin-left: 64px; }
  .main-screen__info h2 {
    width: 470px;
    margin-top: 73px;
    font-size: 60px;
    line-height: 70px; }
  .main-screen__info p {
    width: 370px;
    margin-bottom: 57px;
    padding-left: 48px;
    font-size: 18px;
    line-height: 28px; }
    .main-screen__info p::before {
      width: 33px; }
  .main-screen__image {
    top: 0;
    right: -24px; }
  .main-screen__image img {
    width: 260px;
    height: 340px; }
  .main-screen__button {
    width: 378px;
    margin-bottom: 0;
    margin-left: 52px; }
  .main-screen__scroll {
    display: none; } }

@media (max-width: 767px) {
  .main-screen__info {
    margin-left: 0; }
  .main-screen__info h2 {
    width: 277px;
    margin-top: 185px;
    font-size: 40px;
    line-height: 47px; }
  .main-screen__info p {
    width: 245px;
    margin-bottom: 38px;
    font-size: 16px;
    line-height: 25px; }
  .main-screen__image {
    top: -9px;
    right: -5px; }
  .main-screen__image img {
    width: 294px;
    height: 220px; }
  .main-screen__button {
    width: 287px;
    margin-left: 0; } }

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: bold;
  text-align: center;
  color: #ffffff;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  box-sizing: border-box;
  border-radius: 5px; }

.button__text--mobile {
  display: none; }

@media (max-width: 767px) {
